<template>
    <div class="flash-wrap">
        <div class="flash-dt clearfix">
            <div class="flash-news fl"><em></em></div>
            <div class="flash-content fl">毛鹏皓解盘 — 如何将简单的单的</div>
            <router-link to="/Notice" class="flash-more fr">更多</router-link>
        </div>
        <ul class="flash-dd clearfix">
            <li class="fl" v-for="n in newdata.imgSrc.length">
                <img v-lazy="newdata.imgSrc[n-1]">
                <p>{{newdata.text[n-1]}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'newsflash',
        data() {
            return {
                newdata:{
                    imgSrc:[require('@/assets/xxzd.jpg'),require('@/assets/pxkx.jpg'),require('@/assets/gpxj.jpg'),require('@/assets/zhcx.jpg')],
                    text:['学习终端','培训课程','股票书籍','账户储值']
                }
            }
        },
        methods: {
            
        }
    }
</script>
<style lang="less" scoped>
    .flash-wrap{
        background:#f2f2f2;
        padding-bottom:.2rem;
        .flash-dt{
            width:100%;
            height:.59rem;
            padding:.1rem 0;
            border-bottom:1px solid #eaf0f4;
            background:#fff;
            .flash-news{
                width:2.29rem;
                height:100%;
                position:relative;
                border-right:2px dotted #ccc;
                em{
                    width:1.89rem;
                    height:.45rem;
                    position:absolute;
                    left:0;
                    top:0;
                    bottom:0;
                    right:0;
                    margin:auto;
                    background:url(../../../assets/newsflash.jpg) no-repeat;
                    background-size:100% 100%;
                }
            }
            .flash-content{
                width:3.85rem;
                height:100%;
                padding:0 .2rem;
                line-height:.59rem;
                font-size:.28rem;
                color:#333;
                white-space:nowrap; 
                overflow:hidden; 
                text-overflow:ellipsis; 
                border-right:1px dotted #ccc;
            }
            .flash-more{
                width:.8rem;
                height:.59rem;
                text-align:center;
                line-height:.59rem;
                font-size:.28rem;
                color:#797979;
            }
        }
        .flash-dd{
            width:100%;
            height:2.06rem;
            padding-top:.27rem;
            background:#fff;
            li{
                width:25%;
                text-align:center;
                img{
                    width:1.26rem;
                    height:1.26rem;
                }
                p{
                    font-size:.28rem;
                    color:#333;
                    padding-top:.2rem;
                    line-height:100%;
                    font-weight:400;
                    font-size:.28rem;
                    color:#333;
                }
            }
        }
        .container-main{
            width:100%;
            height:2.33rem;
            background:#fff;
        }
    }
</style>


