<template>
    <div class="home">
        <div v-title data-title="主页">主页</div>
        <home-header :userName="getData"></home-header>
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="Src in imagesArr" key="Src.id">
                <img  v-lazy="Src">
            </mt-swipe-item>
        </mt-swipe>
        <news-flash></news-flash>
        <teacher-column></teacher-column>
        <training-course></training-course>
        <limited-activity></limited-activity>
        <posits-mall></posits-mall>
        <img v-lazy="entranchSrc" class="entranch-main">
        <hot-goods></hot-goods>
    </div>
</template>
<script>
    import HomeHeader from './children/Header.vue';
    import NewsFlash from './children/newsFlash.vue';
    import TeacherColumn from './children/teacherColumn.vue';
    import trainingCourse from './children/trainingCourse.vue';
    import limitedActivity from './children/limitedActivity.vue';
    import positsMall from './children/pointsMall.vue';
    import hotGoods from './children/hotGoods.vue';
    export default {
        name: 'home',
        data() {
            return {
                //轮播是否循环播放
                continuous:true,
                //初始轮播切换索引
                defaultIndex:0,
                //是否显示状态条
                showIndicators:true,
                //触发tachstart时阻止默认行为
                prevent:true,
                //阻止冒泡
                stopPropagation:true,
                imagesArr:[
                    require('@/assets/1.jpg'),
                    require('@/assets/2.jpg'),
                    require('@/assets/3.jpg')
                ],
                entranchSrc:require('@/assets/entrance.jpg'),
                getData:''
            }
        },
        created: function() {
            var userName = this.$route.params.userName;
            this.getData = userName
        },
        components: {
            HomeHeader,
            NewsFlash,
            TeacherColumn,
            trainingCourse,
            limitedActivity,
            positsMall,
            hotGoods
        }
    }
</script>
<style lang="less" scoped>
.home{
    .mint-swipe{
        width:100%;
        height:3.28rem;
        img{
            width:100%;
            height:100%;
        }
        .mint-swipe-indicators{
            .mint-swipe-indicator{
                width:20px;
                height:20px;
            }
        }
    }
    .entranch-main{
        width:100%;
        height:2rem;
    }
}
</style>


