<template>
  <div class="header clearfix">
    <router-link to="{name:home}" class="logo fl"><img v-lazy="logoImg"></router-link>
    <router-link class="input-main fl" to='/search'><input type="text" value="搜索宝贝、课程、讲师..." disabled></router-link>
    <div  @click="toLogoIn" class="scode fl">
        <span>
            <img :src="userImg">
        </span>
        <h3>{{userName}}</h3>
    </div>
    <router-link to="{name:home}" class="scode fl">
        <span class="active"></span>
        <h3>消息中心</h3>
    </router-link>
  </div>
</template>

<script>
    export default {
        name: 'header',
        data() {
            return {
                logoImg:require('@/assets/logo.png'),
                userImg:require('@/assets/scode.png')
            }
        },
        methods: {
            toLogoIn:function() {
                this.$router.push({
                    name:'LogoIn',
                    path:'/LogoIn'
                })
            }
        },
        props:{
            userName:''
        }
    }
</script>

<style lang="less" scoped>
    .header {
        width: 100%;
        height: .62rem;
        background: -webkit-linear-gradient(left, #f97522, #fd5148);
        /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(right, #f97522, #fd5148);
        /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(right, #f97522, #fd5148);
        /* Firefox 3.6 - 15 */
        background: linear-gradient(to right, #f97522, #fd5148);
        /* 标准的语法 */
        padding: .16rem 0;
        overflow: hidden;
        .logo {
            width: 1rem;
            height: 100%;
            position: relative;
            img {
                width: .63rem;
                height: .43rem;
                margin: auto;
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                right: 0;
            }
        }
        .input-main {
            width: 4.7rem;
            height: 100%;
            border-radius: 100px;
            background: rgba(255, 255, 255, .3) url(../../../assets/search.png) no-repeat .24rem center;
            background-size: .26rem .27rem;
            line-height:100%;
            input {
                height: 100%;
                width: 100%;
                border: none;
                background: none;
                text-indent: .7rem;
                font-size: .26rem;
                color: #fff;
            }
        }
        .scode {
            width: .8rem;
            text-align: center;
            padding-top:.4rem;
            position:relative;
            span{
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:.4rem;
                text-align:center;
                img{
                    width:.3rem;
                    height:.3rem
                }
            }
            span.active{
                background:url(../../../assets/news.png) no-repeat center;
                background-size:.38rem .37rem;
            }
            h3 {
                font-size: .15rem;
                color: #fff;
                padding-top: .05rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }
    }
</style>


