<template>
    <div class="hotGoods-wrap">
        <teach-title :title="title" :imgSrc="imgSrc"></teach-title>
        <ul class="content-main clearfix">
            <li class="fl" v-for="(item,index) in content.imhSrc.length" :key="item.id">
                <img v-lazy="content.imhSrc[index]">
                <p class="desi">{{content.desI[index]}}</p>
                <p class="price">￥<span>{{content.price[index]}}</span>.00</p>
            </li>
        </ul>
    </div>    
</template>
<script>
    import teachTitle from './children/teachTitle.vue';
    export default {
        name : 'hotGoods',
        data(){
            return{
                title:'热卖商品',
                imgSrc:require('@/assets/hot.jpg'),
                content:{
                    imhSrc:[require('@/assets/con16.jpg'),require('@/assets/con17.jpg'),require('@/assets/con18.jpg'),require('@/assets/con18.jpg')],
                    desI:['学习终端+PC端MINI','炒股入门必读股票投资炒股入门必读股票投资','董钟祥的股票教室开课','爱股轩100点券在线充值'],
                    price:[2980,16,2980,10]
                }
            }
        },
        components:{
            teachTitle
        }
    }
</script>
<style lang="less" scoped>
    .hotGoods-wrap{
        background:#f2f2f2;
        .content-main{
            li{
                width:3.7rem;
                height:4.96rem;
                background:#fff;
                margin-top:.1rem;
                margin-right:.08rem;
                img{
                    width:3.7rem;
                    height:3.7rem;
                }
                .desi{
                    font-size:.3rem;
                    color:#333;
                    padding:.2rem .2rem;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                }
                .price{
                    padding-left:.2rem;
                    color:#e02828;
                    font-size:.3rem;
                    span{
                        font-size:.36rem;
                    }
                }
            }
            li:nth-child(2n){
                margin-right:0;
            }
        }
    }
</style>


